body {
	margin:0;
	padding:0;
    font-family:helvetica, arial, sans-serif;
    background: #9ad197;
    background: -webkit-linear-gradient(top, #9ad197 0%, #7bc3b1 60%, #5cb4cb 100%);
    background: linear-gradient(to bottom, #9ad197 0%, #7bc3b1 60%, #5cb4cb 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#9ad197', endColorstr='#5cb4cb', GradientType=0);
}
.container {
    width:100%;
    max-width:1000px;
    margin:auto;
    padding:0 15px;
    box-sizing: border-box;
    font-style:normal;
}
.txtYellow {
    color:#fedc32;
}
.txtCenter {
    text-align:center !important;
}
.error {
    background:pink;
}
header {
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    height: 80px;
    padding: 0;
    display:flex;
    align-items:center;
    background: #e2e2e4;
    box-sizing: border-box !important;
    -moz-box-sizing: border-box !important;
    -webkit-box-sizing: border-box !important;
    box-shadow: 1px 6px 5px -1px rgba(0, 0, 0, 0.4);
    z-index: 99;
}
header figure {
    height: 80px;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
}
header .logo {
    margin: 0;
    height: 60%;
    display: block;
    position: relative;
}
main {
	padding:90px 0 45px 0;
}
main h1 {
	color: #ffffff;
    font-size: 56px;
    margin: 0 0px 45px 0px;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    display: block;
    font-weight: 700;
    line-height: 1;
    padding:0;
}
main h2 {
    text-align: center;
    color: #ffffff;
    margin: -20px auto 40px auto;
    font-size: 32px;
    padding: 0 32px 0 16px;
    line-height: 1.125;
    font-weight: 500;
}
@media(max-width:992px) {
	main {
		padding:45px 0 30px 0;
	}
	main h1 {
		font-size:50px;
	}
	main h2 {
		font-size:26px;
		text-align:left;
		padding:0;
	}
}
.checkRow {
    display:-webkit-flex;
    display:flex;
    padding:90px 0 0 0;
}
.checkRow article {
    width:33.33%; 
    text-align:left;
    color:#fff;
}
.checkRow img {
    width:15px;
    display:inline-block;
    margin-right:5px;
}
@media(max-width:992px) {
    .checkRow {
        padding: 45px 0 0 0;
        -webkit-flex-direction:column;
                flex-direction:column;
    }
    .checkRow article {
        width:100%;
        text-align:center;
        margin:0 0 10px 0;
    }
}
footer {
    display: -webkit-flex;
    display: flex;
	height: auto;
    line-height: 30px;
    font-size: 13px;
    background: #3f4347;
    color: #ffffff;
    clear: both;
    padding: 20px 0;
}
.disclaimer {
	font-size: 13px;
    color: #ffffff !important;
    line-height: 18px;
    margin-bottom: 0px;
    text-align: left !important;
    font-family:helvetica, arial, sans-serif;
    font-weight:100;
}
footer a {
	color:#fff;
}
footer img {
    margin-right:5px;
}
.icon_facebook {
    height:16px;
}
.icon_twitter {
    height:13px;
}
.icon_linkedin {
    height:15px;
}
.icon_google {
    height:13px;
}
.calcForm p {
    color: #fff;
    font-size: 25px;
    line-height: 1.325;
    margin: 40px 0 0 0;
}
.calcForm ul {
    color: #fff;
    font-size: 25px;
    line-height: 1.325;
    color: #fedc32 !important;
    margin-left: 40px;
}
.calcForm li {
	font-size: 25px;
    color: #fedc32;
    line-height: 1.325;
    background-image: none;
    list-style: disc outside none;
    list-style: initial;
    padding: 0;
}
.calcForm li span {
	color:#fff;
}
@media(max-width:992px) {
    .calcForm ul {
        margin-left: 0;
        padding: 0 0 0 24px;
    }
    .calcForm li {
        font-size:22px;
    }
}
.signUpForm .inputWrapper{
	display:-webkit-flex;
	display:flex;
	-webkit-flex-flow: row wrap;
	        flex-flow: row wrap;
}
.signUpForm {
	margin-top:55px;
}
.signUpForm input {
	width:43.6%;
	height:40px;
	line-height: 40px;
	border-radius:5px;
	margin:0 0 15px 0;
	padding:0 10px;
	border:0px;
	font-size:16px;
}
.signUpForm input:first-child {
	margin-right:3%;
}
@media(max-width:992px) {
 .signUpForm {
    margin-top:45px;
 }
 .signUpForm input {
    width:100%;
    margin:0 0 15px 0 !important;
 }   
}
.signUpForm button {
	width:100%;
}
.signUpForm .submit {
    margin: 0;
    height: 40px;
    border-radius: 5px;
    border: 0px;
    color: #3f4347;
    background: #fedc32;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
}
.successMessage {
    display:none;
}
#successMessage.show {
    display:block;
    color: #fff;
    font-size: 22px;
    margin: 15px 0 0 0;
}
.calculatorSection {
	display: -webkit-flex;
	display: flex;
    width: 90%;
    margin: auto;
}
.sectionLeft {
	width:50%;
	padding-right:45px;
}
.sectionRight {
	width:50%;
}
@media(max-width:992px) {
	.calculatorSection {
		-webkit-flex-direction:column;
		        flex-direction:column;
		width:100%;
	}
	.sectionLeft {
		width:100%;
		padding-right:0;
	}
	.sectionRight {
		width:100%;
	}
}
.calculator {
    padding: 15px;
    border-radius: 10px;
    background: #3f4347;
    margin-bottom: 100px;
    box-shadow: 0px 2px 5px 3px rgba(0, 0, 0, 0.17);
    position: relative;
}
@media(max-width:992px) {
	.calculator {
		margin:0;
	}
}
.calculator input[readonly] {
     cursor: pointer !important;
     pointer-events: none;
}
.calculator figure {
    padding: 0;
    margin: 0;
}
.calculator img {
    width: 20px;
}
.calculator figcaption {
	float: right;
    font-weight: 700;
    color: #ffffff;
}
.calculator h2 {
	color: #ffffff;
    font-size: 20px;
    font-weight: 500;
    text-align: left;
    margin: 15px 0;
    padding: 0;
}
.calculator input {
    width: 100%;
    height: 50px;
    box-sizing: border-box;
    border: 0;
    border-radius: 5px;
    font-size: 18px;
    text-align: center;
    color: #3f4347;
    padding-top: 2px
}
.calculator button {
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    line-height: 35px;
    border-radius: 5px;
    background: #fedc32;
    margin-top: 20px;
    color: #3f4347;
    font-size: 18px;
    font-weight: bold;
    position: relative;
    display: block;
    text-decoration: none;
    margin: 0 auto;
    border-radius: 5px;
    border: 1px solid #fedc32;
    background: #fedc32;
    text-align: center;
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
    box-shadow: 0px 6px 0px #d0ae04;
    margin: 15px 0 25px 0px;
    cursor:pointer;
}
.calculator button:active {
    box-shadow: 0px 2px 0px #d0ae04;
    position: relative;
    top: 7px;
}
.calculator .output {
    background: #ffffff;
    border-radius: 5px;
    height: 30px;
    line-height: 30px;
}
.calcTableWrap {
	border-collapse:collapse; 
	color:#ffffff; 
	font-size:15px; 
	width:100%
}
.rowOnecolumnOne {
	width:30%; 
	border-bottom:1px solid #cdcdcd;
}
.rowOnecolumnTwo {
	width:35%; 
	border-left:1px solid #cdcdcd; 
	border-bottom:1px solid #cdcdcd; 
	padding-bottom:8px; 
	vertical-align:bottom;
	text-align:center;
}
.rowOnecolumnThree {
	width:35%; 
	border-bottom:1px solid #cdcdcd; 
	border-left:1px solid #cdcdcd; 
	border-right:1px solid #cdcdcd; 
	text-align:center; 
	padding:8px;
}
.rowTwoColumnOne {
	width:30%; 
	padding:10px 0 0 0;
}
.rowTwoColumnTwo {
	width:35%; 
	border-left:1px solid #cdcdcd; 
	padding-top:10px;
	text-align:center;
}
.rowTwoColumnThree {
	width:35%; 
	border-left:1px solid #cdcdcd; 
	border-right:1px solid #cdcdcd; padding-top:10px;
	text-align:center;
}
.rowThreeColumnOne {
	width:30%; 
	border-bottom:1px solid #cdcdcd; 
	padding:10px 0 10px 0;
}
.rowThreeColumnTwo {
	width:35%; 
	border-left:1px solid #cdcdcd; 
	border-bottom:1px solid #cdcdcd; 
	padding-top:0px;
	text-align:center;
}
.rowThreeColumnThree {
	width:35%; 
	border-bottom:1px solid #cdcdcd; 
	border-left:1px solid #cdcdcd; 
	border-right:1px solid #cdcdcd;
	text-align:center;
}
.rowFourColumnOne {
	width:30%; 
	padding:10px 0;
}
.rowFourColumnTwo {
	width:35%; 
	border-left:1px solid #cdcdcd; 
	padding:8px 0; 
	vertical-align:middle;
	text-align:center;
	cursor:default;
	position:relative;
}
.rowFourColumnTwo .output {
	width:80%;
}
.rowFourColumnThree {
	vertical-align:middle; 
	width:35%; 
	padding:8px 0; 
	border-left:1px solid #cdcdcd; 
	border-right:1px solid #cdcdcd;
	text-align:center;
	cursor:default;
	position:relative;
}
.rowFourColumnTwo:after,
.rowFourColumnThree:after {
	content:" ";
	position:absolute;
	left:0;
	right:0;
	top:0;
	bottom:0;
	z-index:10;
	background:transparent;
}
.rowFourColumnThree .competitor {
	width:80%;
}
